body {
    background-color: #333333;
    height: 100vh;
    overflow: hidden;
}

iframe {
    width: 21cm;
    left: calc(50% - 15.75cm);
    position: absolute;
    padding: 0;
    overflow: visible;
    border: 0;
    height: 100vh;
}

::-moz-selection {
    background-color: rgba(68, 136, 255, 0.25);
}

#options {
    padding: 8px;
    display: inline-block;
    position: absolute;
    z-index: 1;
    opacity: 0;
    pointer-events: none;
    left: calc(50vw + 5.25cm);
    width: 10.5cm;
}

#options.focus {
    opacity: 1;
    pointer-events: all;
}

#first, #second {
    display: block;
    font-family: -apple-system, sans-serif;
    font-size: 12pt;
    padding: 8px;
    border: 2px solid #4488FF;
    outline: none;
    margin: 0;
    color: white;
    position: relative;
    word-break: break-word;
}

#second {
    margin-top: 12px;
}

#second[data-placeholder="undefined"] {
    display: none;
}

#first:empty::after, #second:empty::after {
    color: #888888;
    content: attr(data-placeholder);
}

#first::before, #second::before {
    content: attr(data-placeholder);
    position: absolute;
    padding: 2px;
    transform: translateY(-100%);
    font-size: 9pt;
    background-color: #333333;
}

#second[data-placeholder="text"] + #display {
    display: none;
}

#display {
    display: block;
    font-family: -apple-system, sans-serif;
    font-size: 9pt;
    border: none;
    margin-top: 12px;
    user-select: none;
    color: white;
}

#display > #check {
    border: 2px solid #4488FF;
    display: inline-block;
    height: 1em;
    width: 1em;
    transform: translateY(25%);
}

#display.display > #check {
    background-color: white;
}

#check {
    pointer-events: none;
}